<template>
    <view class="sj-form">
        <slot></slot>
    </view>
</template>

<script>
    export default {
        name:"sj-form",
        data() {
            return {
                
            };
        },
        
        provide() {
            return {
                form: this
            }
        },
        
        props: {
            // 验证规则
            rules: {
                type: Object,
                default() {
                    return {}
                }
            },
            
            // 需要验证的表单字段
            model: {
                type: Object,
                default() {
                    return {}
                },
                required: true
            }
        },
        
        methods: {
            // 验证表单
            validate(callback) {
                let task = this.$children[0].$children.filter(v => v.field).map(v => v.validate())
                Promise.all(task).then(res => {
                    if(res.indexOf(false) == -1){
                        callback && callback()
                    }
                })
            }
        }
    }
</script>

